import React, { useState } from "react";

const EventEx = () => {
	const [form, setForm] = useState({
		username: "",
		message: "",
	});
	const { username, message } = form;
	const onChange = (e) => {
		const nextForm = {
			...form, //     
			[e.target.name]: e.target.value, //   
		};
		setForm(nextForm);
	};
	const onClick = () => {
		alert(username + " : " + message);

		setForm({
			username: "",
			message: "",
		});
	};
	const onKeyPress = (e) => {
		if (e.key === "Enter") {
			onClick();
		}
	};

	return (
		<div>
			<h3> </h3>
			<input
				type="text"
				name="username"
				placeholder=" "
				value={username}
				onChange={onChange}
			/>
			<input
				type="text"
				name="message"
				placeholder=" "
				value={message}
				onChange={onChange}
				onKeyPress={onKeyPress}
			/>
			<button onClick={onClick}></button>
		</div>
	);
};

export default EventEx;
