import React, { useReducer } from "react";

function reducer(state, action) {
	return {
		...state,
		[action.name]: action.value,
	};
}

function InfoReducer(props) {
	const [state, dispatch] = useReducer(reducer, { name: "", nickname: "" });
	const { name, nickname } = state;
	const onChange = (e) => {
		dispatch({ name: e.target.name, value: e.target.value }); //e.target     
	};
	return (
		<div>
			<p>   </p>
			<input
				type="text"
				name="name"
				value={name}
				onChange={onChange}
			/>
			<input
				type="text"
				name="nickname"
				value={nickname}
				onChange={onChange}
			/>
			<div>
				<b>: </b> {name} <br />
				<b>: </b> {nickname}
			</div>
		</div>
	);
}

export default InfoReducer;
