import React, { useState, useEffect, useCallback, useRef } from "react";
import Library from "./ex01/Library";
import Clock from "./ex02/Clock";
import MyComponent from "./ex02/MyComponent";
import Say from "./ex03/Say";
import EventEx from "./ex04/EventEx";
import Counter from "./ex05/Counter";
import CounterReducer from "./ex05/CounterReducer";
import Info from "./ex05/Info";
import InfoReducer from "./ex05/InfoReducer";
import Average from "./ex05/Average";
import "./App.css";
import AccordionMui from "./components/AccordionMui";
import TodoTemplate from "./components/todo/TodoTemplate";
import TodoInsert from "./components/todo/TodoInsert";
import TodoList from "./components/todo/TodoList";

function App() {
	const [visible, setVisible] = useState(false);
	const [todos, setTodos] = useState([
		{ id: 1, text: "", checked: true },
		{ id: 2, text: " ", checked: true },
		{ id: 3, text: "", checked: false },
	]);

	//  
	const nextId = useRef(4);
	const onInsert = useCallback(
		(text) => {
			const todo = {
				id: nextId.current,
				text,
				cheked: false,
			};
			setTodos(todos.concat(todo));
			nextId.current += 1;
		},
		[todos]
	);

	//   
	const onRemove = useCallback(
		(id) => {
			//filter      
			//    
			setTodos(todos.filter((todo) => todo.id !== id));
		},
		[todos]
	);

	//  
	const onToggle = useCallback(
		(id) => {
			setTodos(todos.map((todo) => (todo.id === id ? { ...todo, checked: !todo.checked } : todo)));
		},
		[todos]
	);

	return (
		<>
			<TodoTemplate>
				<TodoInsert onInsert={onInsert} />
				<TodoList
					todos={todos}
					onRemove={onRemove}
					onToggle={onToggle}
				/>
			</TodoTemplate>
			<AccordionMui
				title={
					<>
						<h3>
							chapter 1.
							<p> component, setInterval(), props , defaultProps, event handling</p>
						</h3>
					</>
				}
				detail={
					<>
						<div className="ui-box">
							<h4>component (molecule)</h4>
							<Library />
						</div>
						<div className="ui-box">
							<h4>setInterval</h4>
							<Clock />
						</div>
						<div className="ui-box">
							<h4>props, defaultProps</h4>
							<MyComponent
								nam=""
								age={9}
							/>
						</div>
						<div className="ui-box">
							<h4>useState()</h4>
							<Say></Say>
						</div>
						<div className="ui-box">
							<h4> </h4>
							<EventEx />
						</div>
					</>
				}
			/>
			<AccordionMui
				title={
					<>
						<h3>
							chapter 2. <p>useState(), useEffect(),  , useReducer(), useMemo()</p>
						</h3>
					</>
				}
				detail={
					<>
						<div className="ui-box">
							<h4>hook- useState(), useEffect()</h4>
							<Counter />
							<Info />
						</div>
						<div className="ui-box">
							<h4>hook - useEffect()   </h4>
							<div>
								<button
									onClickCapture={() => {
										setVisible(!visible);
									}}
								>
									{visible ? " " : " "}
								</button>
								{visible && <Info />}
							</div>
						</div>
						<div className="ui-box">
							<h4>hook - useReducer() ex1</h4>
							<p>CounterReducer      switch case  </p>
							<CounterReducer />
						</div>
						<div className="ui-box">
							<h4>hook - useReducer() ex2</h4>
							<InfoReducer />
						</div>
						<div className="ui-box">
							<h4>hook - useMemo() </h4>
							<Average />
						</div>
					</>
				}
			/>
		</>
	);
}

export default App;
