import React, { useState, useEffect } from "react";

const Info = () => {
	const [name, setName] = useState("");
	const [nickname, setNickname] = useState("");

	//     
	// useEffect(() => {
	// 	console.log(" ");
	// 	console.log(name, nickname);
	// });

	// ,[]        
	// useEffect(() => {
	// 	console.log("  ");
	// 	console.log(name, nickname);
	// }, []);

	//    ,    
	// useEffect(() => {
	// 	console.log("  ");
	// 	console.log(name, nickname);
	// }, [name]);

	//useEffect clean up
	useEffect(() => {
		console.log("effect");
		console.log(name);
		return () => {
			console.log("clean up");
			console.log(name);
		};
	}, [name]);

	const onChangeName = (e) => {
		setName(e.target.value);
	};
	const onChangeNickname = (e) => {
		setNickname(e.target.value);
	};

	return (
		<div>
			<p>   </p>
			<input
				type="text"
				name="name"
				value={name}
				onChange={onChangeName}
			/>
			<input
				type="text"
				name="nickname"
				value={nickname}
				onChange={onChangeNickname}
			/>
			<div>
				<b>: </b> {name} <br />
				<b>: </b> {nickname}
			</div>
		</div>
	);
};

export default Info;
